const eventOddVal = (wallId) => {
    if (wallId % 2 == 0) {
        return 'even'
    } else {
        return 'odd'
    }
}

export const brandingUrl = (id, wallId) => {
    let anchorText = 'Website Widget';
    switch (Array.isArray(id) && id != '' && id.length === 1 ? id[0] : 0) {
        case 1:
            if (eventOddVal(wallId) == 'even') {
                anchorText = 'influencer marketing'
            } else {
                anchorText = 'types of  influencer marketing'
            }
            return ['https://taggbox.com/blog/influencer-marketing/', anchorText]
        case 2:
        case 18:
            if (eventOddVal(wallId) == 'even') {
                anchorText = 'Instagram Widget'
            } else {
                anchorText = eventOddVal(Math.floor((wallId / 10) % 10)) == 'even' ? 'Best Instagram Widget' : 'Free Instagram Widget'
            }
            return ['https://taggbox.com/instagram-widget/', anchorText]
        case 3:
            if (eventOddVal(wallId) == 'even') {
                anchorText = 'user generated content'//'Facebook Review Widget'
            } else {
                anchorText = 'UGC'//'Best Facebook Review Widget'
            }
            return ['https://taggbox.com/blog/user-generated-content/', anchorText]
        case 4:
            if (eventOddVal(wallId) == 'even') {
                anchorText = 'Google Review Widget'
            } else {
                anchorText = 'Best Google Review Widget'
            }
            return ['https://taggbox.com/google-reviews-widget/', anchorText]
        case 7:
            return ['https://taggbox.com/blog/user-generated-content/', 'user generated content']
        case 28:
            return ['https://taggbox.com/tiktok-widget/', 'Tiktok Widget']
        case 10:
            return ['https://taggbox.com/blog/user-generated-content/', 'user generated content']
        default:
            return ['https://taggbox.com/widget/', anchorText]
    }
}

